/**
 * @desc Ë¥ùÂ°ûÂ∞îÊõ≤Á∫øÁÆóÊ≥ïÔºåÂåÖÂê´‰∫Ü3Èò∂Ë¥ùÂ°ûÂ∞î
 */
class Bezier {
    /**
     * @desc Ëé∑ÂèñÁÇπÔºåËøôÈáåÂèØ‰ª•ËÆæÁΩÆÁÇπÁöÑ‰∏™Êï∞
     * @param {number} num ÁÇπ‰∏™Êï∞
     * @param {Array} p1 ÁÇπÂùêÊ†á
     * @param {Array} p2 ÁÇπÂùêÊ†á
     * @param {Array} p3 ÁÇπÂùêÊ†á
     * @param {Array} p4 ÁÇπÂùêÊ†á
     * Â¶ÇÊûúÂèÇÊï∞ÊòØ num, p1, p2 ‰∏∫‰∏ÄÈò∂Ë¥ùÂ°ûÂ∞î
     * Â¶ÇÊûúÂèÇÊï∞ÊòØ num, p1, c1, p2 ‰∏∫‰∫åÈò∂Ë¥ùÂ°ûÂ∞î
     * Â¶ÇÊûúÂèÇÊï∞ÊòØ num, p1, c1, c2, p2 ‰∏∫‰∏âÈò∂Ë¥ùÂ°ûÂ∞î
     */
    getBezierPoints(num = 100, p1, p2, p3, p4) {
        let func = null;
        const points = [];
        if (!p3 && !p4) {
            func = this.oneBezier;
        } else if (p3 && !p4) {
            func = this.twoBezier;
        } else if (p3 && p4) {
            func = this.threeBezier;
        }
        for (let i = 0; i < num; i++) {
            points.push(func(i / num, p1, p2, p3, p4));
        }
        if (p4) {
            points.push([...p4]);
        } else if (p3) {
            points.push([...p3]);
        }
        return points;
    }

    /**
     * @desc ‰∏ÄÈò∂Ë¥ùÂ°ûÂ∞î
     * @param {number} t ÂΩìÂâçÁôæÂàÜÊØî
     * @param {Array} p1 Ëµ∑ÁÇπÂùêÊ†á
     * @param {Array} p2 ÁªàÁÇπÂùêÊ†á
     */
    oneBezier(t, p1, p2) {
        const [x1, y1] = p1;
        const [x2, y2] = p2;
        let x = x1 + (x2 - x1) * t;
        let y = y1 + (y2 - y1) * t;
        return [x, y];
    }

    /**
     * @desc ‰∫åÈò∂Ë¥ùÂ°ûÂ∞î
     * @param {number} t ÂΩìÂâçÁôæÂàÜÊØî
     * @param {Array} p1 Ëµ∑ÁÇπÂùêÊ†á
     * @param {Array} p2 ÁªàÁÇπÂùêÊ†á
     * @param {Array} cp ÊéßÂà∂ÁÇπ
     */
    twoBezier(t, p1, cp, p2) {
        const [x1, y1] = p1;
        const [cx, cy] = cp;
        const [x2, y2] = p2;
        let x = (1 - t) * (1 - t) * x1 + 2 * t * (1 - t) * cx + t * t * x2;
        let y = (1 - t) * (1 - t) * y1 + 2 * t * (1 - t) * cy + t * t * y2;
        return [x, y];
    }

    /**
     * @desc ‰∏âÈò∂Ë¥ùÂ°ûÂ∞î
     * @param {number} t ÂΩìÂâçÁôæÂàÜÊØî
     * @param {Array} p1 Ëµ∑ÁÇπÂùêÊ†á
     * @param {Array} p2 ÁªàÁÇπÂùêÊ†á
     * @param {Array} cp1 ÊéßÂà∂ÁÇπ1
     * @param {Array} cp2 ÊéßÂà∂ÁÇπ2
     */
    threeBezier(t, p1, cp1, cp2, p2) {
        const [x1, y1] = p1;
        const [x2, y2] = p2;
        const [cx1, cy1] = cp1;
        const [cx2, cy2] = cp2;
        let x =
            x1 * (1 - t) * (1 - t) * (1 - t) +
            3 * cx1 * t * (1 - t) * (1 - t) +
            3 * cx2 * t * t * (1 - t) +
            x2 * t * t * t;
        let y =
            y1 * (1 - t) * (1 - t) * (1 - t) +
            3 * cy1 * t * (1 - t) * (1 - t) +
            3 * cy2 * t * t * (1 - t) +
            y2 * t * t * t;
        return [x, y];
    }
}

export default new Bezier();

